html {
  position: relative;
  min-height: 100%;
}

body {
  font-family: 'Roboto', serif;
}
#body {
  padding-top: 80px;
  min-height: 100vh;
}

.title.dark-mode, .sub-title.dark-mode {
  color: #2196f3;
  background-color: transparent !important;
}

.title.light-mode, .sub-title.light-mode {
  color: #000000;
  background-color: transparent !important;
}

img {
  object-fit: cover;
}
.input-group {
  border-radius: 1rem;
  box-shadow: 3px 3px 2px rgba(0, 0, 0, 0.1);
}

.form-control {
  border-radius: 1rem;
  border: none;
}

.form-control:focus {
  outline: none !important;
  box-shadow: none;
}


.input-group-text {
  border-radius: 1rem;
  border: none;
  background-color: white;
}

.card {
  margin-bottom: 20px;
  background-color: transparent;
  border: none;
}

.ribbon {
  position: absolute;
  background: #2196f3;
  color: white;
  font-size: 11px;
  font-weight: bold;
  padding: 3px 8px;
  border-radius: .25rem 0 .6em 0;
}

.card-body {
  padding: 0;
}

.card-body > img {
  box-shadow: 3px 3px 2px rgba(0, 0, 0, 0.1);
}

.card-title, .card-title.light-mode {
  margin: 0;
  font-size: 14px;
  font-weight: bold;
  background-color: transparent !important;
}
.card-subtitle, .card-subtitle.light-mode {
  margin: 0;
  font-size: 12px;
  background-color: transparent !important;
}

.card-title.dark-mode, .card-subtitle.dark-mode {
  background-color: transparent !important;
  color: white !important;
}

.footer {
  position:fixed;
  top: 0;
  width: 100%;
  height: 60px;
  border-bottom: 1px solid transparent;
  box-shadow: 3px 3px 2px rgba(0, 0, 0, 0.1);
  z-index: 999;
}

.navbar{
  border-top: 1px solid transparent;
  box-shadow: 3px 3px 2px rgba(0, 0, 0, 0.1);
}

.navbar.dark-mode > span {
  color: #2196f3;
}
.navbar.light-mode > span {
  color: white;
}
.group-container{
  padding:0;
  height:100%;
}

.selectors, .block{
  height:100%;
  width:100%;
}

.selectors a{
  border: 0;
  border-radius: 0;
  width:25%;
  margin: auto auto auto 0;
}

.selectors a:active{
  border:0;
}

.selectors a:focus{
  border:0;
  outline: 0;
  box-shadow: 0 0 0 0;
}

.active, .selector-holder{
  display: flex;
  flex-direction: column;
}

.inactive{
  display: none;
}

.selector-holder span{
  font-size: 0.8rem;
}

.bg-dark {
  background-color: #242526 !important;
}

.dark-mode {
  background-color: #18191a !important;
}
#body.dark-mode {
  background-color: #242526 !important;
}
#body.light-mode {
  background-color: #e9ecef !important;
}
.light-mode {
  background-color: #2196f3 !important;
}
/* Colors of the buttons*/
.button-active, .selectors a:hover, .selectors a:active, .selectors a:focus {
  color: #2196f3;
}
.button-inactive{
  color: #9E9E9E;
}
.selectors.light-mode > a{
  color: #ffffff !important;
}
.selectors.light-mode > .button-inactive{
  color: #bfbfbf !important;
}

.menu {
  height: 50px;
  align-content: center;
}

.menu-text{
  background-color: transparent !important;
  align-self: center;
  font-size: 16px;
}

.menu-text.dark-mode {
  color: white;
  background-color: transparent !important;
}

.switch {
  position: relative;
  display: inline-block;
  width: 60px;
  height: 34px;
  margin: 0;
}

.switch input {
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  border-radius: 34px;
  background-color: #ccc;
  -webkit-transition: .4s;
  transition: .4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 26px;
  width: 26px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  border-radius: 50%;
  -webkit-transition: .4s;
  transition: .4s;
}

input:checked + .slider {
  background-color: #2196F3;
}

input:focus + .slider {
  box-shadow: 0 0 1px #2196F3;
}

input:checked + .slider:before {
  -webkit-transform: translateX(26px);
  -ms-transform: translateX(26px);
  transform: translateX(26px);
}